import React from 'react';
import ReactDOM from 'react-dom';
import axios from 'axios';
import './index.css'


function _defineProperty(obj, key, value) {
  if (key in obj) {
    Object.defineProperty(obj, key, {
      value: value,
      enumerable: true,
      configurable: true,
      writable: true
    });
  } else {
    obj[key] = value;
  }
  return obj;
}
const { Component } = React;
const { render } = ReactDOM;
var val = "all";

const LOAD_STATE = {
  SUCCESS: "SUCCESS",
  ERROR: "ERROR",
  LOADING: "LOADING"
};

class App extends Component {
  constructor() {
    super();
    _defineProperty(this, "myfun", () => {
      console.log("buttonclicked");
      if (document.getElementById("myText").value) {
        val = document.getElementById("myText").value;
      } else {
        val = "all"; //  baseUrl="https://api.unsplash.com/photos/curated";
      }
      this.fetchPhotos(1);
      console.log(val);
    });
    this.state = {
      photos: [],
      totalPhotos: 0,
      perPage: 7,
      currentPage: 1,
      loadState: LOAD_STATE.LOADING
    };
  }

  componentDidMount() {
    this.fetchPhotos(this.state.currentPage);
  }

  fetchPhotos(page) {
    var self = this;
    const { perPage } = this.state;
    const { appId, baseUrl } = this.props;
    const options = {
      params: {
        client_id: appId,
        page: page,
        per_page: perPage,
        query: val
      }
    };

    this.setState({ loadState: LOAD_STATE.LOADING });
    axios
      .get(baseUrl, options)
      .then(response => {
        self.setState({
          photos: response.data.results,
          totalPhotos: parseInt(response.headers["x-total"]),
          currentPage: page,
          loadState: LOAD_STATE.SUCCESS
        });
      })
      .catch(() => {
        this.setState({ loadState: LOAD_STATE.ERROR });
      });
  }
  // myfun = () => {
  // console.log("buttonclicked")
  // if (document.getElementById("myText").value){
  //     val=document.getElementById("myText").value;}
  // else{
  //   val="all";
  //   baseUrl="https://api.unsplash.com/photos/curated";
  // }
  // this.fetchPhotos(1);
  // console.log(val);
  // }

  render() {
    return React.createElement(
      "div",
      { className: "app" },
      React.createElement(
        "div",
        null,
        React.createElement("input", {
          type: "text",
          defaultValue: "Search",
          name: "search",
          id: "myText"
        }),
        React.createElement(
          "button",
          { type: "search", onClick: this.myfun },
          " \uD83D\uDD0D"
        )
      ),

      React.createElement("div", { id: "value" }),
      React.createElement(Pagination, {
        current: this.state.currentPage,
        total: this.state.totalPhotos,
        perPage: this.state.perPage,
        onPageChanged: this.fetchPhotos.bind(this)
      }),

      this.state.loadState === LOAD_STATE.LOADING
        ? React.createElement("div", { className: "loader" })
        : React.createElement(List, { data: this.state.photos })
    );
  }
}

const ListItem = ({ photo }) => {
  return React.createElement(
    "div",
    { key: photo.id, className: "grid__item card" },
    React.createElement(
      "div",
      { className: "card__body" },
      React.createElement("img", { src: photo.urls.small, alt: "" })
    ),

    React.createElement(
      "div",
      { className: "card__footer media" },
      React.createElement("img", {
        src: photo.user.profile_image.small,
        alt: "",
        className: "media__obj"
      }),
      React.createElement(
        "div",
        { className: "media__body" },
        React.createElement(
          "a",
          { href: photo.user.portfolio_url, target: "_blank" },
          photo.user.name
        )
      )
    )
  );
};

const List = ({ data }) => {
  var items = data.map(photo =>
    React.createElement(ListItem, { key: photo.id, photo: photo })
  );
  return React.createElement("div", { className: "grid" }, items);
};

class Pagination extends Component {
  pages() {
    var pages = [];
    for (var i = this.rangeStart(); i <= this.rangeEnd(); i++) {
      pages.push(i);
    }
    return pages;
  }

  rangeStart() {
    var start = this.props.current - this.props.pageRange;
    return start > 0 ? start : 1;
  }

  rangeEnd() {
    var end = this.props.current + this.props.pageRange;
    var totalPages = this.totalPages();
    return end < totalPages ? end : totalPages;
  }

  totalPages() {
    return Math.ceil(this.props.total / this.props.perPage);
  }

  nextPage() {
    return this.props.current + 1;
  }

  prevPage() {
    return this.props.current - 1;
  }

  hasFirst() {
    return this.rangeStart() !== 1;
  }

  hasLast() {
    return this.rangeEnd() < this.totalPages();
  }

  hasPrev() {
    return this.props.current > 1;
  }

  hasNext() {
    return this.props.current < this.totalPages();
  }

  changePage(page) {
    this.props.onPageChanged(page);
  }

  render() {
    return React.createElement(
      "div",
      { className: "pagination" },
      React.createElement(
        "div",
        { className: "pagination__left" },
        React.createElement(
          "a",
          {
            href: "#",
            className: !this.hasPrev() ? "hidden" : "",
            onClick: e => this.changePage(this.prevPage())
          },
          "Prev"
        )
      ),

      React.createElement(
        "div",
        { className: "pagination__mid" },
        React.createElement(
          "ul",
          null,
          React.createElement(
            "li",
            { className: !this.hasFirst() ? "hidden" : "" },
            React.createElement(
              "a",
              { href: "#", onClick: e => this.changePage(1) },
              "1"
            )
          ),

          React.createElement(
            "li",
            { className: !this.hasFirst() ? "hidden" : "" },
            "..."
          ),

          this.pages().map((page, index) => {
            return React.createElement(
              "li",
              { key: index },
              React.createElement(
                "a",
                {
                  href: "#",
                  onClick: e => this.changePage(page),
                  className: this.props.current === page ? "current" : ""
                },
                page
              )
            );
          }),

          React.createElement(
            "li",
            { className: !this.hasLast() ? "hidden" : "" },
            "..."
          ),
          React.createElement(
            "li",
            { className: !this.hasLast() ? "hidden" : "" },
            React.createElement(
              "a",
              { href: "#", onClick: e => this.changePage(this.totalPages()) },
              this.totalPages()
            )
          )
        )
      ),

      React.createElement(
        "div",
        { className: "pagination__right" },
        React.createElement(
          "a",
          {
            href: "#",
            className: !this.hasNext() ? "hidden" : "",
            onClick: e => this.changePage(this.nextPage())
          },
          "Next"
        )
      )
    );
  }
}
Pagination.defaultProps = {
  pageRange: 2
};

render(
  React.createElement(App, {
    appId: "7365d310ecda312a8f87c73098d546ad80d1c5223812b1eee99ce1f10c830cd1",
    baseUrl: "https://api.unsplash.com/search/photos"
  }),

  document.getElementById("mount-point")
);
